/* =========================================
   PAGE LAYOUT
========================================= */
.contact {
  padding: 50px 0;
  display: flex;
  justify-content: center;
}

/* =========================================
   CONTACT CARD
========================================= */
.contact-card {
  width: 100%;
  max-width: 720px;
  padding: 34px 28px;
  text-align: center;

  background: rgba(255, 255, 255, 0.05);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);

  border: 1px solid rgba(255, 255, 255, 0.07);
  border-radius: 18px;

  box-shadow: 0 10px 28px rgba(0, 0, 0, 0.35);

  position: relative;
  animation: fadeUp 0.8s ease-out;
}

/* Glowing Border */
.contact-card::before {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 18px;
  padding: 2px;

  background: linear-gradient(135deg, #6b5cff, #7df0d9);
  -webkit-mask:
     linear-gradient(#fff 0 0) content-box,
     linear-gradient(#fff 0 0);
  -webkit-mask-composite: xor;
          mask-composite: exclude;

  animation: glow 4s linear infinite;
}

/* =========================================
   TITLES
========================================= */
.contact-title {
  font-size: 32px;
  font-weight: 700;

  background: linear-gradient(90deg, #6b5cff, #7df0d9);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;

  margin-bottom: 10px;
}

.contact-sub {
  font-size: 15px;
  color: var(--muted);
  max-width: 520px;
  margin: 0 auto 24px;
  line-height: 1.6;
}

/* =========================================
   ACTION BUTTONS
========================================= */
.contact-actions {
  display: flex;
  gap: 14px;
  justify-content: center;
  flex-wrap: wrap;
}

/* Base Button */
.contact-btn {
  padding: 12px 20px;
  border-radius: 12px;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;

  display: inline-flex;
  align-items: center;
  justify-content: center;

  transition: 0.3s ease;
}

/* Primary Button */
.primary-btn {
  background: linear-gradient(90deg, #6b5cff, #7df0d9);
  color: #000;
}

.primary-btn:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 26px rgba(0, 255, 200, 0.3);
}

/* Outline Buttons */
.outline-btn {
  background: rgba(255, 255, 255, 0.05);
  color: #7df0d9;
  border: 1px solid rgba(125, 240, 217, 0.4);
}

.outline-btn:hover {
  background: rgba(125, 240, 217, 0.15);
  transform: translateY(-5px);
}

/* =========================================
   ANIMATIONS
========================================= */
@keyframes glow {
  0% { filter: hue-rotate(0deg); }
  100% { filter: hue-rotate(360deg); }
}

@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(12px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* =========================================
   RESPONSIVE DESIGN
========================================= */
@media (max-width: 600px) {
  .contact-title {
    font-size: 26px;
  }

  .contact-btn {
    width: 100%;
  }
}