/* =========================================
   PAGE HEADER
========================================= */
.projects-head {
  margin: 24px 0 20px;
  text-align: center;
}

.section-title {
  font-size: 34px;
  font-weight: 700;
  margin-bottom: 6px;
}

.section-sub {
  font-size: 15px;
  color: var(--muted);
}

/* =========================================
   GRID LAYOUT
========================================= */
.projects-grid {
  display: grid;
  grid-template-columns: 260px 1fr;
  gap: 24px;
  align-items: start;
  margin-top: 20px;
}

/* =========================================
   PROJECT LIST (LEFT)
========================================= */
.projects-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  padding: 14px;

  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);

  border-radius: 14px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

/* Project List Item */
.proj-item {
  background: rgba(255, 255, 255, 0.03);
  border: 1px solid rgba(255, 255, 255, 0.05);

  padding: 14px;
  border-radius: 12px;
  text-align: left;
  cursor: pointer;

  transition: 0.25s ease;
  color: var(--muted-2);
}

.proj-item:hover {
  background: rgba(255, 255, 255, 0.08);
  transform: translateY(-2px);
}

/* Active Project */
.proj-item.is-active {
  background: linear-gradient(135deg, #6b5cff, #7df0d9);
  color: #0e0220;
  box-shadow: 0 4px 18px rgba(0, 255, 200, 0.3);
  transform: translateY(-3px);
}

.proj-title {
  font-weight: 700;
  margin-bottom: 4px;
}

.proj-sub {
  font-size: 0.85rem;
  opacity: 0.85;
}


/* =========================================
   PROJECT DETAIL PANEL
========================================= */
.proj-detail {
  display: grid;
  grid-template-columns: 1fr 360px;
  gap: 20px;
  padding: 20px;

  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(10px);
  border-radius: 16px;
  border: 1px solid rgba(255, 255, 255, 0.08);
}

/* Main Preview */
.proj-preview img {
  width: 100%;
  height: 320px;
  object-fit: contain;
  border-radius: 12px;
  background: rgba(255, 255, 255, 0.04);
  padding: 6px;
  border: 1px solid rgba(255, 255, 255, 0.06);
}

/* Meta Info */
.proj-meta {
  padding-top: 4px;
}

.proj-name {
  font-size: 22px;
  font-weight: 700;
}

.proj-sub-title {
  font-size: 14px;
  color: var(--muted);
  margin-bottom: 10px;
}

.proj-desc {
  font-size: 14px;
  color: var(--muted-2);
  line-height: 1.6;
}


/* =========================================
   ACTION BUTTONS
========================================= */
.proj-actions {
  display: flex;
  gap: 14px;
  margin-top: 18px;
}

.btn {
  padding: 10px 16px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 14px;
  cursor: pointer;
  transition: 0.25s ease;
  text-decoration: none;
}

/* Primary button */
.btn-primary {
  background: linear-gradient(90deg, #6b5cff, #7df0d9);
  color: #000;
}

.btn-primary:hover {
  transform: translateY(-4px);
  box-shadow: 0px 6px 20px rgba(0, 255, 200, 0.35);
}

/* Outline button */
.btn-outline {
  border: 1px solid rgba(125, 240, 217, 0.4);
  color: #7df0d9;
  background: rgba(255, 255, 255, 0.03);
}

.btn-outline:hover {
  background: rgba(125, 240, 217, 0.12);
  transform: translateY(-4px);
}


/* =========================================
   THUMBNAILS
========================================= */
.thumbs-row {
  display: flex;
  gap: 10px;
  margin-top: 14px;
  overflow-x: auto;
  padding-bottom: 6px;
}

.thumb {
  width: 76px;
  height: 56px;
  border-radius: 10px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  background: rgba(255, 255, 255, 0.03);
  overflow: hidden;
  cursor: pointer;
  transition: 0.2s ease;
}

.thumb:hover {
  transform: translateY(-3px);
  background: rgba(255, 255, 255, 0.1);
}

.thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}


/* =========================================
   LIGHTBOX
========================================= */
.lb-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.85);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.lb-inner {
  position: relative;
  max-width: 1100px;
  width: 92%;
  padding: 22px;
  display: flex;
  align-items: center;
  gap: 14px;
  background: rgba(20, 20, 30, 0.65);
  backdrop-filter: blur(14px);
  border-radius: 16px;
}

.lb-image {
  max-width: 100%;
  max-height: 78vh;
  object-fit: contain;
}

.lb-close {
  position: absolute;
  top: 12px;
  right: 12px;
  background: rgba(255,255,255,0.12);
  border-radius: 100%;
  border: 0;
  padding: 6px 10px;
  color: white;
  cursor: pointer;
}

.lb-nav {
  background: rgba(255,255,255,0.08);
  border: 0;
  padding: 12px 16px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 20px;
  transition: 0.25s ease;
}

.lb-nav:hover {
  background: rgba(255,255,255,0.18);
}

.lb-count {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  color: var(--muted);
}


/* =========================================
   RESPONSIVE DESIGN
========================================= */

@media (max-width: 900px) {
  .projects-grid {
    grid-template-columns: 1fr;
  }

  .proj-detail {
    grid-template-columns: 1fr;
  }

  .proj-preview img {
    height: 260px;
  }
}

@media (max-width: 600px) {
  .proj-actions {
    flex-direction: column;
  }

  .btn {
    width: 100%;
    text-align: center;
  }

  .projects-list {
    gap: 10px;
  }
}
/* ============================
   FILTER BAR
============================ */
.filter-bar {
  display: flex;
  gap: 12px;
  margin: 20px 0;
  flex-wrap: wrap;
}

.filter-btn {
  padding: 8px 18px;
  border-radius: 10px;
  font-size: 14px;
  background: rgba(255,255,255,0.08);
  border: 1px solid rgba(255,255,255,0.15);
  color: var(--muted-2);
  cursor: pointer;
  transition: 0.25s ease;
}

.filter-btn:hover {
  background: rgba(255,255,255,0.15);
  transform: translateY(-2px);
}

.filter-btn.active {
  background: var(--accent-grad);
  color: black;
  box-shadow: var(--glow);
}
